// TITLE "RUSSO ONE"

@font-face {
	font-family: 'RussoOne';
	src: url('./../fonts/russoone-regular-webfont.woff2') format('woff2');
	src: url('./../fonts/russoone-regular-webfont.woff')format('woff');
	font-weight: 400;
	font-style: normal;
}

// ==========




// PARAGRAPH "ROBOTO"

@font-face {
	font-family: 'Roboto';
	src: url('./../fonts/roboto-light-webfont.eot');
	src: url('./../fonts/roboto-light-webfont.eot?#iefix') format('embedded-opentype'),
			 url('./../fonts/roboto-light-webfont.woff2') format('woff2'),
			 url('./../fonts/roboto-light-webfont.woff') format('woff');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto';
	src: url('./../fonts/roboto-regular-webfont.eot');
	src: url('./../fonts/roboto-regular-webfont.eot?#iefix') format('embedded-opentype'),
			 url('./../fonts/roboto-regular-webfont.woff2') format('woff2'),
			 url('./../fonts/roboto-regular-webfont.woff') format('woff');
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto';
	src: url('./../fonts/roboto-regularitalic-webfont.eot');
	src: url('./../fonts/roboto-regularitalic-webfont.eot?#iefix') format('embedded-opentype'),
			 url('./../fonts/roboto-regularitalic-webfont.woff2') format('woff2'),
			 url('./../fonts/roboto-regularitalic-webfont.woff') format('woff');
	font-weight: 400;
	font-style: italic;
}

