//SKILL

/*skill*/

.fourth-screen {
	background-color: $light-bg;
}



/*-----------*/


/*Skill block*/

.skill__block {
	display: flex;
	flex-direction: row;
	@include bp-1200 {
		flex-direction: column;
		align-items: center;
	}
}

.skill__menu {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	flex-grow: 1;
	margin-right: 20px;
	@include bp-1200 {
		flex-direction: row;
		margin-right: 0;
	}
	@include bp-480 {
		flex-direction: column;
		margin-bottom: 5px;
	}
}

.skill__menu-block {
	display: flex;
	flex-direction: row;
	width: 380px;
	@include bp-1200 {
		width: auto;
		justify-content: center;
	}
	@include bp-480 {
	}
}

.skill__item {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	flex-basis: 180px;
	height: 148px;
	padding: 30px 40px;
	background: white;
	margin: 5px;
	transition-duration: 0.3s;
	box-shadow: 0px 3px 5px 0 rgba(120, 125, 100, 0.1);
	span {
		color: #5D88D0;
	}
	&:hover {
		background-color: #d4e494;
		text-decoration: none;
		cursor: pointer;
		span {
			
			color: $green;
		}
	}
	@include bp-1200 {
		padding: 10px 15px;
		font-size: 12px;
		height: auto;
	}
	@include bp-480 {
		margin: 0;
	}
	img {
		margin-bottom: 8px;
		@include bp-1200 {
			height: 25px;
		}
	}
}

.skill__item-box {
	display: flex;
	flex-direction: row;
	@include bp-480 {
		margin-right: 5px;
	}
}

.home {
	@include bp-480 {
		margin-right: 5px;
		margin-bottom: 5px;
	}
}

.summer {
	@include bp-480 {
		margin-bottom: 5px;
	}
}


.pergola {
	width: 90px;
	padding: 15px;
	margin-right: 0;
	@include bp-1200 {
		margin-right: 5px;
	}
}

.sauna {
	width: 85px;
	padding: 15px;
}

.skill--active {
	background-color: $green;
	span {
		color: $white;
	}
}

/*-----------*/


/*Skill description*/

.skill__description {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: stretch;
	position: relative;
	flex-grow: 2;
	height: 306px;
	margin: 5px;
	background-color: $white;
	box-shadow: 0px 3px 5px 0 rgba(120, 125, 100, 0.1);
	@include bp-640 {
		flex-direction: column;
		height: auto;
	}
	@include bp-480 {
		max-width: 322px;
		margin: 0;
	}
	@include bp-320 {
	}
}

.description__arrow {
	display: flex;
	align-items: center;
	@include bp-1200 {
		display: none;
	}
}

.description__arrow-box {
	position: absolute;
	left: -10px;
	width: 0; 
	height: 0; 
	border-top: 10px solid transparent;
	border-bottom: 10px solid transparent; 
	border-right:10px solid $white;
}

.description__text {
	padding: 40px 50px;
	flex-grow: 1;
	width: 340px;
	p {
		font-size: 18px;
		font-family: 'RussoOne';
		margin-bottom: 10px;
		color: $text;
		@include bp-640 {
			font-size: 18px;
			margin-bottom: 5px;
		}
	}
	@include bp-640 {
		width: 100%;
		padding: 30px 25px;
	}
}

.description-number {
	font-size: 45px;
	font-family: 'RussoOne';
	color: $green;
	@include bp-640 {
		display: none;
	}
}

.description-info {
	color: #35332f;
	line-height: 1.3;
}

.description__pic {
	overflow: hidden;
	flex-grow: 2;
	flex-shrink: 2;
	img {
		width: 100%;
		height: 100%;
	}

}

// HEADER END ==========


