//FOOTER

/*Footer*/

footer {
	justify-content: space-between;
	background-color: #1F2715;

}

.footer-wrap {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-top: 30px;
	padding-bottom: 30px;
	@include bp-1200 {
		flex-direction: column-reverse;
	}
}

.footer__logo {
	display: flex;
	flex-direction: row;
	flex-grow: 2;
	a {
		margin-right: 15px;
		@include bp-480 {
			margin-right: 0;
		}
		img {
			@include bp-480 {
				width: 120px;
			}
		}
	}
	span {
		color: #4b563d;
		font-size: 12px;
		width: 140px;
		@include bp-480 {
			text-align: center;
			width: auto;
		}
	}
	@include bp-480 {
		flex-direction: column;
		text-align: center;
	}

}

.footer__contacts {
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-grow: 1;
	@include bp-1200 {
		margin-bottom: 25px;
	}
	@include bp-768 {
		flex-direction: column-reverse;
	}
	@include bp-640 {
		margin-bottom: 0;
	}
}

.contacts__block {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include bp-768 {
		margin-bottom: 20px;
	}
	@include bp-480 {
		flex-direction: column-reverse;
		margin-bottom: 0;
	}
}


.contacts__block .contacts__phone,
.contacts__block .contacts__social {
	display: block;
}

.contacts__block .contacts__social {
	@include bp-480 {
		margin-right: 0 !important;
		margin-bottom: 15px;
	}
}

.contacts__block .contacts__address {
	@include bp-480 {
		text-align: center;
	}
}

.footer__contacts .contacts__phone {
	flex-basis: auto;
	@include bp-480 {
		margin-bottom: 15px;
		a {
			padding-left: 0;

			&::before {
				display: none;
			}
		}
	}
}

.footer__contacts .contacts__social {
	width: 70px;
	margin-right: 35px;
}

.contacts__block > .contacts__address {
	@include bp-480 {
		margin-bottom: 15px;
	}
}

.contacts__block .contacts__mail {
	@include bp-480 {
		margin-bottom: 15px;
		margin-right: 0;
	}
}



.contacts__address {
	display: flex;
	flex-direction: row;
	margin-right: 35px;
	width: 160px;
	color: $white;
	@include bp-640 {
		margin-right: 0;
	}
}

.contacts__address-ic {
	display: flex;
	align-items: center;
	position: relative;
	margin-right: 10px;
	@include bp-480 {
		display: none;
	}

}	

.contacts__mail {
	display: flex;
	flex-direction: row;
	margin-right: 35px;
	color: $white;
}

.contacts__mail-ic {
	margin-right: 10px;
	display: flex;
	align-items: center;
	@include bp-480 {
		display: none;
	}
}	

