//FINAL

/*Name block*/

.seventh-screen {
	background: url('../img/footer-bg.jpg') no-repeat;
	background-color: $main-bg;
	background-size: cover;
}

.final__title {
	width: 580px;
	h2 {
		font-family: 'RussoOne';
		font-size: 40px;
		color: white;
		margin-bottom: 20px;
		font-weight: normal;
		@include bp-640 {
			font-size: 22px;
			margin-bottom: 10px;
		}
		@include bp-480 {
			font-size: 18px;
		}
	}
	p {
		font-size: 20px;
		color: white;
		margin-bottom: 40px;
		@include bp-640 {
			font-size: 16px;
		}
		@include bp-480 {
			font-size: 14px;
			margin-bottom: 20px;
		}
	}
	@include bp-992 {
		width: 100%;
		text-align: center;
	}
}

/*-----------*/


/*Form*/

.footer-form {
	width: 100%;
	padding: 30px 60px;
	border-radius: 10px;
	@include bp-480 {
		padding: 25px 35px;
	}
}

.footer-form .form-title {
	padding-bottom: 10px;
	font-size: 18px;
	@include bp-480 {
		font-size: 14px;
	}
}

.footer__inputs {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 10px;
	@include bp-992 {
		flex-direction: column;
	}
}

.footer__items {
	display: flex;
	flex-direction: row;
	margin-bottom: 0;
	& div:first-child {
		margin-left: 0;
	}
	& div:last-child {
		margin-right: 0;
	}
	@include bp-992 {
		margin-bottom: 5px;
	}
	@include bp-640 {
		flex-direction: column;
	}
	@include bp-480 {
		flex-direction: column;
	}
}

.footer__items .main-input {
	margin: 5px;
	@include bp-992 {
		width: 100%;
	}
	@include bp-640 {
		margin: 0 0 10px 0;
	}
}

.footer-btn {
	margin: 5px;
	height: 45px;
	width: 280px;
	@include bp-992 {
		width: 100%;
		margin: 0 0 10px 0;
	}

}

.footer__agree-check {
	text-align: center;
	@include bp-480 {
		text-align: left;
	}
}



