//PORTFOLIO

/*Name block*/

.third-screen {
	position: relative;
}



/*-----------*/


/*Cards*/

.portfolio__menu {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 40px;
	@include bp-640 {
		margin-bottom: 20px;
	}
}

.portfolio__filter {
	border: 1px solid $dark-bg;
	border-radius: 25px;
	@include bp-640 {
		display: flex;
		flex-direction: column;
		width: 100%;
		border-radius: 15px;
		overflow: hidden;
	}
	.btn {
		color: $text;
	}
}

.cards__container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
}
  
.portfolio__cards {
	display: none; 
	flex-basis: 340px;
	margin: 15px;
	transition-duration: 0.3s;
	color: $text;
	background-color: $light-bg;
	cursor: pointer;
	@include bp-640 {
		padding-left: 0;
		padding-right: 0;
		padding-bottom: 10px;
		padding-top: 0;
		height: auto;
	}
	&:hover {
		background-color: $green;
		color: $white !important;
		text-decoration: none;
		box-shadow: 0px 5px 20px #DBE6B1;
		fill: $white;

	}
}

.portfolio__cards:hover .card__info .card__arrow .arrow__box svg {
	fill: white;
}


.show {
	display: block;
}
  
.btn {
	border: none;
	outline: none;
	padding: 12px 16px;
	border-radius: 25px;
	background-color: white;
	cursor: pointer;
	transition-duration: 0.3s;
	@include bp-640 {
		border-radius: 0;
		padding-top: 8px;
		padding-bottom: 8px;
	}
}

.btn:hover {
	background-color: #ddd;
}

.btn.active {
	background-color: $green;
	color: white;
}

.card__img {
	img {
		width: 100%;
		height: 226px;
	}
}

.card__info {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: stretch;
	padding: 20px 15px;
}

.card__text {
	span {
		font-size: 11px;
		text-transform: uppercase;
	}
	p {
		font-family: 'RussoOne';
		font-size: 15px;
		margin-top: 4px;
	}
}

.card__arrow {
	display: flex;
	align-items: center;
	position: relative;
	width: 15px;
}

.arrow__box {
	position: relative;
	width: 15px;
	height: 15px;
	fill: $green;
}

/*-----------*/


/*Card text*/


.card__description {
	display: none;
	justify-content: center;
	align-items: center;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 1005;
	background-color: rgba(0,0,0,0.4);
}

.animate__animated.animate__fadeIn {
	--animate-duration: 0.5s;
  }

.card__description-block {
	position: relative;
	background-color: #fff;
	width: 1000px;
	padding: 50px 35px;
	margin: 0 auto;
	margin-left: 15px;
	margin-right: 15px;
	h2 {
		font-family: 'Russo One';
		margin-bottom: 25px;
	}
	@include bp-1200 {
		width: auto;
		padding: 43px 0 0 0;
		background: #F8F8F8;
	}

}

.description-block__close {
	position: absolute;
	right: 0;
	top: 0;
	width: 30px;
	height: 30px;
	span {
		display: inline-block;
		transform: rotate(45deg);
		font-size: 35px;
		cursor: pointer;
		color: #999;
	}
}

.description-box {
	display: flex;
	flex-direction: row;
	@include bp-1200 {
		flex-direction: column;
	}
}

.description-block__img {
	max-width: 600px;
	margin-right: 30px;
	img {
		width: 100%;
		height: auto;
	}
	@include bp-1200 {
		max-width: 100%;
		margin-right: 0;
	}

}

.description-block__text {
	box-sizing: border-box;
	background: #F8F8F8;
	width: 300px;
	padding: 35px 30px;
	@include bp-1200 {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: column;
	}
	@include bp-380 {
		padding: 20px 20px;
	}
}

.project__title {
	margin-bottom: 30px;
	p {
		font-size: 32px;
		font-family: 'Russo One', 'Open Sans', Arial;
		@include bp-380 {
			font-size: 26px;
		}
	}
	@include bp-1200 {
		text-align: center;
	}
	@include bp-480 {
		margin-bottom: 15px;
	}
}

.project__table {
	margin-bottom: 10px;
	table, th, td {
		border: none;
	}
	th {
		padding-bottom: 10px;

	}
	td {
		padding-right: 60px;
		padding-bottom: 10px;
		@include bp-380 {
			padding-bottom: 4px;
		}
	}
	@include bp-380 {
		font-size: 13px;
	}
}

.table-two {
	padding-right: 0 !important;
}

.project__price {
	margin-bottom: 20px;
	p {
		font-size: 32px;
		font-weight: bold;
		color: #98B61D;
		@include bp-380 {
			font-size: 26px;
		}
	}
}

.proj-btn {
	width: 240px;
	padding-top: 14px;
	margin-bottom: 0;
	&:hover {
		text-decoration: none;
		color: #fff;
	}
	@include bp-380 {
		height: 40px;
		padding-top: 11px;
	}
}

/*-----------*/







