//ADVANTAGES

/*Advantages*/

.advantages-wrap {
	padding-top: 60px;
	overflow: hidden;
	@include bp-768 {
		padding-top: 40px;
	}
}

/*-----------*/


/*Advantages info*/

.h2__title {
	img {
		margin-bottom: 5px;
		@include bp-640 {
			height: 12px;
		}
	}
}

.advantages__block {
	display: flex;
	flex-direction: row;
	align-items: center;
	@include bp-992 {
		align-items: flex-end;
	}
}

.advantages__left {
	flex: 2;
	flex-basis: 450px;
	margin-right: 40px;
	margin-bottom: 40px;
	@include bp-992 {
		flex-basis: 100%;
	}
	@include bp-640 {
		margin-right: 0;
	}
	@include bp-480 {
		margin-bottom: 20px;
	}
}

.advantages__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-top: 10px;
	padding-bottom: 10px;
	@include bp-992 {
		width: 480px;
	}
	@include bp-640 {
		width: 100%;
		padding-top: 5px;
		padding-bottom: 5px;
	}
}

.advantages__ic {
	padding: 10px;
	img {
		height: 22px;
		@include bp-480 {
			height: 10px;
		}
	}
	@include bp-480 {
		padding: 0 10px 0 0;
	}
}

.advantages__text {
	p {
		font-size: 18px;
		font-family: 'RussoOne';
		color: #706F6D;
		@include bp-480 {
			font-size: 14px;
		}
	}
	span {
		font-size: 14px;
		color: $text;
		@include bp-480 {
			font-size: 13px;
		}
	}
}

.advantages__right {
	flex: 1;
	flex-basis: 400px;
	@include bp-992 {
	}
	@include bp-640 {
		display: none;
	}
}

.advantages__pic {
	position: relative;
	width: 600px;
	height: 300px;
	img {
		position: absolute;
		right: 0;
		width: 730px;
		background-color: blue;
		z-index: -1;
		@include bp-992 {
		}
	}
}

/*-----------*/


/*Qualities*/

.qualities-container {
	background: $light-bg;
}

.qualities-wrap {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	padding-top: 20px;
	padding-bottom: 20px;
	@include bp-480 {
		flex-direction: column;
	}
}

.qualities-block {
	display: flex;
	flex-direction: row;
	flex: 1;
	@include bp-380 {
		flex-direction: column;
	}
}

.qualities-item {
	display: flex;
	flex-direction: row;
	align-items: center;
	flex: 1;
	margin: 20px;
	p {
		font-size: 16px;
		flex-basis: 200px;
		color: $text;
		@include bp-992 {
			font-size: 14px;
		}
		@include bp-480 {
			font-size: 13px;
			flex-basis: 100%;
		}
		
	}
	@include bp-992 {
		margin: 5px;
	}
	@include bp-480 {
	}
}

.qualities__ic {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	margin-right: 10px;
	img {
		height: auto;
		@include bp-992 {
			height: 30px;
		}
		@include bp-480 {
			height: 25px;
		}
	}
	@include bp-480 {
		width: 30px;
		height: 30px;
	}
}

/*-----------*/


