//BANNER

/*banner wrap*/

.banner-wrap {
	display: flex;
	padding-top: 140px;
	padding-bottom: 140px;
	@include bp-992 {
		flex-direction: column;
		align-items: center;
		padding-top: 80px;
		padding-bottom: 80px;
	}
	@include bp-768 {
		padding-top: 60px;
		padding-bottom: 40px;
	}
}

.banner__left {
	display: flex;
	flex-direction: column;
	flex-grow: 2;
	flex-shrink: 2;
	@include bp-992 {
		margin-bottom: 50px;
	}
	@include bp-480 {
		margin-bottom: 30px;
	}
}

.banner__right {
	display: flex;
	justify-content: flex-end;
	flex-grow: 1;
	@include bp-992 {
		width: 100%;
	}
}

/*-----------*/


/*Banner Title*/

.main__title {
	margin-bottom: 60px;
	h1 {
		// Размер шрифта и его настройки в файле Core.scss
		max-width: 700px;
		margin-bottom: 25px;
	}
	p {
		color: $white;
		width: 500px;
		font-size: 20px;
	}
	@include bp-992 {
		text-align: center;
		margin-bottom: 40px;
		h1 {
			max-width: 100%;
		}
		p {
			width: 100%;
		}
	}
	@include bp-640 {
		h1 {
			margin-bottom: 15px;
		}
		p {
			font-size: 16px;
		}
	}
	@include bp-480 {
		margin-bottom: 30px;
		p {
			font-size: 14px;
		}
	}
}

/*-----------*/


/*Buildings*/

.banner__buildings {
	display: flex;
	flex-direction: row;
	& div:last-child {
		margin-right: 0;
	}
	@include bp-992 {
		justify-content: center;
	}
	@include bp-480 {
	}
}

.buildings__item {
	padding-top: 30px;
	padding-left: 14px;
	padding-right: 14px;
	padding-bottom: 30px;
	text-align: center;
	background: rgba(51, 60, 28, 0.8);
	margin-right: 3px;
	img {
		margin-bottom: 15px;
	}
	p {
		color: $white;
	}
	@include bp-480 {
		padding-top: 10px;
		padding-left: 6px;
		padding-right: 6px;
		padding-bottom: 10px;
		img {
			height: 25px;
			margin-bottom: 5px;
		}
		p {
			font-size: 12px;
		}
	}
}

/*-----------*/


/*Main form*/

.main-form {
	position: relative;
	width: 340px;
	padding-top: 40px;
	padding-left: 30px;
	padding-right: 30px;
	padding-bottom: 15px;
	background-color: $white;
	@include bp-992 {
		padding-bottom: 40px;
		width: 100%;
	}
	@include bp-320 {
		padding-top: 25px;
		padding-bottom: 25px;
		padding-left: 20px;
		padding-right: 20px;
	}
}

.form-title {
	text-align: center;
	padding-bottom: 30px;
	p {
		font-family: 'RussoOne';
		font-size: 17px;
		color: $text;
	}
	span {
		font-size: 14px;
	}
	@include bp-992 {
		padding-bottom: 15px;
	}
	@include bp-480 {
		p {
			font-size: 14px;
		}
		span {
			font-size: 13px;
		}
	}
}

.input__item {
	margin-bottom: 10px;
	& input:last-child {
		margin-bottom: 0;
	}
}

.main-input {
	input {
		display: block;
		height: 45px;
		width: 100%;
		padding-left: 40px;
		font-size: 13px;
		margin-bottom: 5px;
		border: none;
		background-color: $dark-bg;
		outline:none;
		border: 1px solid $dark-bg;
		transition-duration: 0.3s;
		&:focus {
			border: 1px solid #98B61D;
			background-color: #fff;
		}
	}
	margin-bottom: 5px;
}

.input-name {
	position: relative;
	&:before {
		position: absolute;
		top: 16px;
		left: 15px;
		content: '';
		background: url('../img/ic_form-user.png') no-repeat;
		width: 10px;
		height: 11px;
	}
}

.input-phone {
	position: relative;
	&:before {
		position: absolute;
		top: 15px;
		left: 16px;
		content: '';
		background: url('../img/ic_form-phone.png') no-repeat;
		width: 9px;
		height: 14px;
	}
}

.input-email {
	position: relative;
	&:before {
		position: absolute;
		top: 17px;
		left: 14px;
		content: '';
		background: url('../img/ic_form-mail.png') no-repeat;
		width: 13px;
		height: 11px;
	}
}

.main-btn {
	@include button($green, $green);
	margin-bottom: 10px;
	@include bp-992 {
		width: 100%;
		margin-bottom: 20px;
	}
	@include bp-480 {
		font-size: 14px;
	}
}

.agree-check {
	font-size: 11px;
	color: rgba(53, 51, 47, 0.5);
	display: flex;
	justify-content: center;
	input {
		cursor: pointer;
	}
	label {
		cursor: pointer;
	}
}

.agree-check__box {
	display: flex;
	align-items: center;
}

.styled-checkbox {
	position: absolute;
	opacity: 0; 
	& + label {
		position: relative;
		cursor: pointer;
		padding: 0;
	}
	& + label:before {
		content: '';
		position: relative;
		top: -2px;
		margin-right: 10px;
		display: inline-block;
		vertical-align: text-top;
		width: 20px;
		height: 20px;
		background: white;
		border-radius: 5px;
		border: 1px solid #CCCAC6;
		transition-duration: 0.3s;
	}
	&:hover + label:before {
		background: #98B61D;
	}
	&:checked + label:before {
		background: #98B61D;
		border: 1px solid #98B61D;
	}
	&:disabled + label {
		color: #b8b8b8;
		cursor: auto;
	}
	&:disabled + label:before {
		box-shadow: none;
		background: #ddd;
	}
	&:checked + label:after {
		content: '';
		position: absolute;
		left: 5px;
		top: 7px;
		background: white;
		width: 2px;
		height: 2px;
		box-shadow: 
		2px 0 0 white,
		4px 0 0 white,
		4px -2px 0 white,
		4px -4px 0 white,
		4px -6px 0 white,
		4px -8px 0 white;
		transform: rotate(45deg);
	}	
}

.triangle {
	position: absolute;
	left: 0;
	bottom: -20px;
	width: 100%; 
	height: 0; 
	border-left: 160px solid transparent;
	border-right: 160px solid transparent;
	border-top: 20px solid $white;
	@include bp-992 {
		display: none;
	}
}


