@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  box-sizing: border-box;
  margin: 0 auto;
}

@mixin adaptive-video() {
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
}




// MAIN BUTTON

@mixin button($bg: $first, $borderColor: $second) {
	display: block;
	width: 280px;
	height: 50px;
	border: 2px solid $borderColor;
	border-radius: 5px;
	box-shadow: none;
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	font-weight: 900;
	text-transform: uppercase;
	text-align: center;
	-webkit-appearance: none;
   	-moz-appearance:    none;
   	appearance:         none;
	transition-property: background-color, border-color;
	transition-duration: 500ms;
	cursor: pointer;
	background-color: $bg;
	color: $white;
	@content;
	&:hover, &:active {
		background-color: lighten($bg, 10%);
		border-color: lighten($bg, 10%);
	}
}




// ADAPTIVE

@mixin bp-1400 {
	@media only screen and (max-width: 1400px) {
		@content;
	}
}
  
