//HEADER

/*First-screen*/

.first-screen {
	background: $main-bg;
	background: url('../img/main-bg.jpg') no-repeat;
	background-size: cover;
}

.header--fixed {
	position: fixed;
	width: 100%;
	background-color: $main-bg;
	z-index: 60;
}

.section-padding {
	padding-top: 50px;
}

.header-container {
	border-bottom: 1px solid #74726A;
}

.header-wrap {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
	padding-top: 20px;
	padding-bottom: 20px;
	transition-duration: 0.3s;
	@include bp-768 {
		padding-top: 10px;
		padding-bottom: 10px;
	}
}

.header-wrap--mini {
	padding-top: 2px;
	padding-bottom: 4px;
}

/*-----------*/


/*Logo*/

.header__logo {
	margin-right: 20px;
	flex-basis: 140px;
	flex-grow: 10;
	flex-shrink: 1;
	@include bp-480 {
		flex-grow: 1;
	}
}

/*-----------*/


/*Menu*/

.header__nav {
	display: flex;
	flex-basis: 353px;
	margin-right: 50px;
	flex-grow: 1;
	justify-content: flex-end;
	& div:first-child {
		padding-left: 0;
	}
	& div:last-child {
		padding-right: 0;
	}
	@include bp-992 {
		display: none;
	}
}

.header__nav-item {
	padding-left: 15px;
	padding-right: 15px;
	a {
		color: $white;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		transition-duration: 0.3s;
		&:hover {
			text-decoration: none;
			color: rgb(159, 168, 148);
		}
	}
}

/*-----------*/


/*Hamburger menu*/

.hamburger-block {
	position: relative;
	flex-grow: 1;
	flex-basis: 30px;
	height: 30px;
	margin-left: 25px;
	@include bp-992 {
		display: block;
	}
}

.hamburger {
	position: absolute;
	display: block;
	z-index: 110;
	right: 0;
	top: 0;
	width: 30px;
	height: 30px;
	cursor: pointer;
}

.menuBtn {
	display: block;
	position: absolute;
	width: 25px;
	height: 30px;
  }
  
.menuBtn span,
.menuBtn::before,
.menuBtn::after {
	display: block;
	position: absolute;
	width: 25px;
	height: 2px;
	background: $white;
	-webkit-transition: all .2s ease-in-out;
	-moz-transition: all .2s ease-in-out;
	-ms-transition: all .2s ease-in-out;
	-o-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}

.menuBtn span {
	top: 50%;
	margin-top: 0;
}

.menuBtn::before,
.menuBtn::after {
	content: "";
}

.menuBtn::before {
	top: 7px;
}

.menuBtn::after {
	bottom: 5px;
}

.menuBtn.type1.close span {
	opacity: 0;
}

.menuBtn.type1.close::before {
	top: 15px;
	-moz-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.menuBtn.type1.close::after {
	bottom: 13px;
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

/*-----------*/


/*Open menu*/

  .overlay {
	position: fixed;
	z-index: 100;
	background: $main-bg;
	top: 0;
	left: 0;
	width: 100%;
	height: 0%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .35s, visibility .35s, height .35s;
	overflow: hidden;
	&.open {
	  opacity: .9;
	  visibility: visible;
	  height: 100%;
	  li {
		animation: fadeInRight .5s ease forwards;
		animation-delay: .35s;
		&:nth-of-type(2) {
		  animation-delay: .4s;
		}
		&:nth-of-type(3) {
		  animation-delay: .45s;
		}
		&:nth-of-type(4) {
		  animation-delay: .50s;
		}
	  }
	}
	nav {
	  position: relative;
	  height: 70%;
	  top: 50%;
	  transform: translateY(-50%);
	  font-size: 50px;
	  font-weight: 400;
	  text-align: center;
	}
	ul {
	  list-style: none;
	  padding: 0;
	  margin: 0 auto;
	  display: inline-block;
	  position: relative;
	  height: 100%; 
	  li {
		display: block;
		height: 25%;
		height: calc(100% / 4);
		min-height: 50px;
		position: relative;
		opacity: 0;
		a {
		  display: block;
		  position: relative;
		  text-decoration: none;
		  overflow: hidden;
		  color: $white;
		  transition-duration: 0.3s;
		  &:hover{
			color: rgb(159, 168, 148);
			}
		}
	  }
	}
  }

  @keyframes fadeInRight {
	0% {
	  opacity: 0;
	  left: 20%;
	}
	100% {
	  opacity: 1;
	  left: 0;
	}
  }


/*-----------*/


/*Contacts*/

.header__contacts {
	display: flex;
	flex-grow: 1;
	justify-content: flex-end;
}

.contacts__social {
	margin-right: 50px;
	width: 70px;
	a {
		margin: 5px;
	}
	@include bp-640 {
		display: none;
	}
}

.contacts__phone {
	flex-basis: 190px;
	a {
		position: relative;
		font-size: 18px;
		color: $white;
		font-family: 'RussoOne';
		padding-left: 20px;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 2px;
			background: url('../img/ic-phone.png') no-repeat;
			width: 10px;
			height: 17px;
		}
		&:hover {
			text-decoration: none;
		}
	}
	@include bp-480 {
		display: none;
	}
}

/*-----------*/


// HEADER END ==========


