//REVIEW

/*Name block*/

.fifth-screen {
	background-color: $dark-bg;
}

/*-----------*/


/*Review block*/

.review-wrap .h2__title {
	margin-bottom: 10px;
	@include bp-480 {
		margin-bottom: 4px;
	}
}

.review__block {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.review__item {
	background-color: $white;
	width: 500px;
	height: 280px;
	padding: 30px;
	margin: 15px;
	box-shadow: 0px 3px 5px 0 rgba(120, 125, 100, 0.1);
	@include bp-768 {
		height: auto;
	}
	&:focus {
		outline: none;
	}
}

.review__name {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 20px;
	p {
		font-size: 18px;
		font-weight: 700;
		color: $text;
		@include bp-640 {
			font-size: 14px;
		}
	}
	&:after {
		position: absolute;
		top: 0;
		right: 0;
		content: '';
		width: 80px;
		height: 60px;
		background: red;
		background: url('../img/ic-quote.jpg') no-repeat;
		@include bp-640 {
			display: none;
		}
	}
	@include bp-640 {
		margin-bottom: 10px;
	}
}

.review__avatar {
	margin-right: 15px;
	img {
		width: 60px;
		height: 60px;
		border-radius: 50%;
		@include bp-640 {
			width: 35px;
			height: 35px;
		}
	}

}

.review__text {
	font-size: 16px;
	font-style: italic;
	line-height: 1.6;
	p {
		color: $text;
	}
	@include bp-480 {
		font-size: 13px;
	}
}

/*Slik button*/

.slick-next,
.slick-prev {
	position: absolute;
	z-index: 2;
	width: 55px;
	padding: 8px 15px 10px 15px;
	height: 50px;
	border-radius: 5px;
	border: solid 1px #C4C4C3;
	transition-duration: 0.3s ;
	&::before {
		color: $text;
	}
	&:hover {
		background-color: #c4c4c3;
	}

}

.slick-next {
	position: relative;
	right: -5px;
	top: 20px;
}

.slick-prev {
	position: relative;
	left: -5px;
	top: 20px;
}

.slick-dots li button:before {
	font-size: 20px;

}


/*-----------*/


// HEADER END ==========

