//STEP

/*Step block*/

.step__title {
	position: relative;
	margin-bottom: 0;
	z-index: 2;
	h2 {
		display: inline-block;
		border-left: 15px solid white;
		border-right: 15px solid white;
		border-bottom: 15px solid white;
		background-color: white;
		@include bp-480 {
			border: none;
			background: none;
		}
		@include bp-480 {
			position: static;
			margin-bottom: 10px;
		}
	}
}

.step__block {
	position: relative;
	top: -22px;
	display: flex;
	flex-direction: row;
	border: 5px solid #EAE9E9;
	padding: 30px 25px;
	@include bp-992 {
		flex-direction: column;
		padding: 20px 15px;
	}
	@include bp-480 {
		top: 0;
		position: static;
		border: none;
		padding-top: 0;
		padding-bottom: 0;
	}
}

.step__box {
	display: flex;
	flex-direction: row;
	@include bp-480 {
		flex-direction: column;
	}
}

.step__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-grow: 1;
	margin: 15px;
	@include bp-992 {
		flex-basis: 50%;
	}
	@include bp-640 {
		margin: 10px;
	}
}

.step__ic {
	img {
		width: 55px;
		margin-right: 15px;
		@include bp-992 {
			width: 40px;
		}
	}
}

.step__text {
	span {
		display: inline-block;
		border-radius: 50%;
		padding: 4px 8px;
		color: #AAA6A6;
		border: 1px solid #C4C4C3;
		margin-bottom: 5px;
	}
	p {
		font-size: 18px;
		color: $text;
		@include bp-640 {
			font-size: 14px;
		}
	}
}

